.click-area {
  display: -ms-inline-flexbox;
  display: inline-flex;
  cursor: pointer;
  -ms-flex-direction: column;
      flex-direction: column;
}

.small {
  font-size: calc(1.5vh + 1.5vw);
}

.white {
  color: #fff;
  -webkit-transition: color 0.15s cubic-bezier(.19,.34,.81,.67);
  -o-transition: color 0.15s cubic-bezier(.19,.34,.81,.67);
  transition: color 0.15s cubic-bezier(.19,.34,.81,.67);
}

.click-area:hover .white{
  background-color: #fff;
  color: #ff1d8e;
}

.right {
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.title {
  -ms-flex-item-align: center;
      align-self: center;
  font-size: calc(4vh + 4vw);
  letter-spacing: calc(1vw + 1vh);
  margin: 0;
  text-transform: uppercase;
}

@media (min-width: 720px) {
  .title {
    font-size: calc(7vh + 7vw);
  }
}


